.table-container {
  overflow-x: auto;

  table.user-table {
    padding: 10px;
    display: flex;
    flex-direction: column;
    min-height: 500px;
    width: 750px;

    tr {
      display: flex;
      padding: 8px 0;
      &.selected {
        background-color: #eee;
      }
    }

    overflow-x: auto;
    overflow-y: hidden;

    tbody {
      display: flex;
      flex-direction: column;
      width: 100%;
      overflow-y: auto;
      overflow-x: hidden;
    }
    thead {
      font-weight: 600;
      td {
        padding-left: 2px;
      }
    }
  }

  td {
    min-width: 250px;

    &.select {
      min-width: 60px;
    }

    &.id {
      min-width: 30px;
    }

    &.name {
      min-width: 0;
      width: 150px;
    }
    &.role {
      min-width: 0;
      width: 100px;
    }
    &.actions {
      min-width: 120px;
    }

    display: inline-block;
    text-align: start;
  }
}
.search-container {
  display: flex;
  justify-content: center;
  .search {
    font-size: 1rem;
    padding: 10px;
    width: 100%;
    max-width: 400px;
    margin: auto;
  }
}
.actions-container {
  display: flex;
  justify-content: center;
  margin: 20px 0;
}

.delete-selected {
  padding: 2px 8px;
}

.App {
  margin: 0 auto;
  max-width: 800px;
}
