.paginator {
  display: flex;
  width: 100%;
  justify-content: space-evenly;
  align-items: center;

  max-width: 600px;
  margin: 0 auto;

  .all-pages {
    width: 160px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    row-gap: 10px;

    > button {
      border-radius: 50%;
      border: 1px solid black;
      margin: 0 5px;
      &:disabled {
        border-color: #ddd;
      }
    }
  }
}
