.row {
  .actions {
    display: flex;
    button {
      border-radius: 50%;
      border: 0;
      background-color: transparent;
      min-width: 40px;
    }
  }
  .save {
    display: none;
  }
  &.editable {
    .edit {
      display: none;
    }
    .save {
      display: initial;
    }
  }
}
